<template>
	<div>
		<el-dialog class="edit_form" v-model="approvalVisible" :title="'ÂÆ°Ê†∏'" width="70%" destroy-on-close>
			<el-form ref="ruleFormRef" :model="approvalForm" label-width="120px" :rules="rules">
				<el-row>
				    <slot name="formItem" :row="approvalForm"></slot>
					<el-col :span="24">
						<el-form-item label="ÂÆ°Ê†∏ÂõûÂ§ç" prop="shhf">
							<el-input v-model="approvalForm.shhf" type="textarea" />
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer>
				<span class="dialog-footer" v-if="sfshType==1">
					<el-button type="danger" @click="approvalSave('Âê¶')">ÊãíÁªù</el-button>
					<el-button type="primary" @click="approvalSave('ÊòØ')">
						ÈÄöËøá
					</el-button>
				</span>
				<span class="dialog-footer" v-if="sfshType==2">
					<el-button type="danger" @click="approvalVisible=false">ÂèñÊ∂à</el-button>
					<el-button type="primary" @click="approvalSave()">
						ÂõûÂ§ç
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>

<script setup>
	import {
		ref,
		toRefs,
		getCurrentInstance,
		defineEmits,
        useAttrs,
	} from 'vue';
	const emit = defineEmits(['shChange'])
    const attrs = useAttrs()    //defineEmits‰∏≠Êú™Ê≥®ÂÜåÁöÑ‰∫ã‰ª∂ÂèØÂú®attrs‰∏≠Ëé∑Âèñ
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	//props
	const props = defineProps({
		tableName: String
	})
	const {
		tableName
	} = toRefs(props)
	//props

	const rules = ref({
		shhf: [{
			required: true,
			message: 'ËØ∑ËæìÂÖ•ÂÆ°Ê†∏ÂõûÂ§ç',
			trigger: 'blur'
		}, ],
	})
	const approvalForm = ref({})
	const approvalVisible = ref(false)
	//ref
	const ruleFormRef = ref(null)
	const sfshType = ref(1)

	const approvalClick = (row,type=1) => {
		sfshType.value = type
		approvalForm.value = JSON.parse(JSON.stringify(row))
		approvalVisible.value = true
	}

	//Â£∞ÊòéÁà∂Á∫ßË∞ÉÁî®
	defineExpose({
		approvalClick,
        approvalVisible
	})
	const approvalSave = (type) => {
		if(sfshType.value == 1){
			approvalForm.value.sfsh = type
		}
		ruleFormRef.value.validate((valid) => {
			if (valid) {
                if(attrs.onApprovalSave){    //Â¶ÇÊûúÊúâÊåáÂÆöÂú®Â§ñÈÉ®Â§ÑÁêÜÔºåÂàôËß¶ÂèëapprovalSave‰∫ã‰ª∂ÔºåÂπ∂return
                    emit('approvalSave',approvalForm.value)
                    return
                }
				let url = `${tableName.value}/update`
				context?.$http({
					url: url,
					method: 'post',
					data: approvalForm.value
				}).then(res => {
					context.$message.success('ÂÆ°Ê†∏ÊàêÂäü')
                    approvalVisible.value = false
					emit('shChange',type,approvalForm.value)
				})
			}
		})
	}
</script>

