<script setup>
	import axios from 'axios'
	const moment = window.moment
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		onMounted,
		watch,
		computed,
	} from 'vue'
	import {
		useRoute,
		useRouter
	} from 'vue-router'
	import {
		useStore
	} from 'vuex';
	const store = useStore()
	const user = computed(()=>store.getters['user/session'])
	const avatar = computed(()=>store.getters['user/avatar'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const baseUrl = ref(context.$config.url)
	import formModel from './formModel.vue'
	//Âü∫Á°Ä‰ø°ÊÅØ
	const tableName = 'bisaibaoming'
	const formName = 'ÊØîËµõÊä•Âêç'
	const route = useRoute()
    const router = useRouter()
	const role = context.$toolUtil.storageGet('sessionTable')
	//Âü∫Á°Ä‰ø°ÊÅØ
	onMounted(()=>{
	})
	//ÂàóË°®Êï∞ÊçÆ
	const list = ref(null)
	const table = ref(null)
	const listQuery = ref({
		page: 1,
		limit: 20,
		sort: 'id',
		order: 'desc'
	})
	const searchQuery = ref({})
	const selRows = ref([])
	const listLoading = ref(false)
	const listChange = (row) =>{
		nextTick(()=>{
			//table.value.clearSelection()
			table.value.toggleRowSelection(row)
		})
	}
	//ÂàóË°®
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		params['sort'] = 'id'
		params['order'] = 'desc'
		if(searchQuery.value.saishimingcheng&&searchQuery.value.saishimingcheng!=''){
			params['saishimingcheng'] = '%' + searchQuery.value.saishimingcheng + '%'
		}
		if(searchQuery.value.bisaishijian&&searchQuery.value.bisaishijian!=''){
			params['bisaishijian'] = '%' + searchQuery.value.bisaishijian + '%'
		}
		if(searchQuery.value.bisaichangdi&&searchQuery.value.bisaichangdi!=''){
			params['bisaichangdi'] = '%' + searchQuery.value.bisaichangdi + '%'
		}
		if(searchQuery.value.sfsh && searchQuery.value.sfsh!=''){
			params['sfsh'] = searchQuery.value.sfsh
		}
		context.$http({
			url: `${tableName}/page`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	//Âà†
	const delClick = (id,row={}) => {
		let ids = []
		if (id) {
			ids = [id]
		} else {
			if (selRows.value.length) {
				for (let x in selRows.value) {
					ids.push(selRows.value[x].id)
				}
			} else {
				return false
			}
		}
		ElMessageBox.confirm(`ÊòØÂê¶Âà†Èô§ÈÄâ‰∏≠${formName}`, 'ÊèêÁ§∫', {
			confirmButtonText: 'ÊòØ',
			cancelButtonText: 'Âê¶',
			type: 'warning',
		}).then(() => {
			context.$http({
				url: `${tableName}/delete`,
				method: 'post',
				data: ids
			}).then(res => {
				context?.$toolUtil.message('Âà†Èô§ÊàêÂäü', 'success',()=>{
					getList()
				})
			})
		}).catch(_ => {})
	}
	//Â§öÈÄâ
	const handleSelectionChange = (e) => {
		selRows.value = e
	}
	//ÂàóË°®Êï∞ÊçÆ
	//ÂàÜÈ°µ
	const total = ref(0)
	const layouts = ref(["total","prev","pager","next"])
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//ÂàÜÈ°µ
	//ÊùÉÈôêÈ™åËØÅ
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
    const approvalSave = async (form)=>{
		context.$http.post(`${tableName}/update`,form).then(res => {
            context.$message.success('ÂÆ°Ê†∏ÊàêÂäü')
            approvalRef.value.approvalVisible = false
			searchClick()
        })
    }
	//ÊêúÁ¥¢
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	//Ë°®Âçï
	const formRef = ref(null)
	const formModelChange=()=>{
		searchClick()
	}
	const addClick = ()=>{
		formRef.value.init()
	}
	const editClick = (id=null,row={})=>{
		if(id){
			formRef.value.init(id,'edit')
			return
		}
		if(selRows.value.length){
			formRef.value.init(selRows.value[0].id,'edit')
		}
	}

	const infoClick = (id=null)=>{
		if(id){
			formRef.value.init(id,'info')
		}
		else if(selRows.value.length){
			formRef.value.init(selRows.value[0].id,'info')
		}
	}
	// Ë°®Âçï
	// È¢ÑËßàÊñá‰ª∂
	const preClick = (file) =>{
		if(!file){
			context?.$toolUtil.message('Êñá‰ª∂‰∏çÂ≠òÂú®','error')
		}
		window.open(context?.$config.url + file)
	}
	// ‰∏ãËΩΩÊñá‰ª∂
	const download = (file) => {
		if(!file){
			context?.$toolUtil.message('Êñá‰ª∂‰∏çÂ≠òÂú®','error')
		}
		let arr = file.replace(new RegExp('file/', "g"), "")
		axios.get((location.href.split(context?.$config.name).length>1 ? location.href.split(context?.$config.name)[0] :'') + context?.$config.name + '/file/download?fileName=' + arr, {
			headers: {
				token: context?.$toolUtil.storageGet('Token')
			},
			responseType: "blob"
		}).then(({
			data
		}) => {
			const binaryData = [];
			binaryData.push(data);
			const objectUrl = window.URL.createObjectURL(new Blob(binaryData, {
				type: 'application/pdf;chartset=UTF-8'
			}))
			const a = document.createElement('a')
			a.href = objectUrl
			a.download = arr
			// a.click()
			// ‰∏ãÈù¢Ëøô‰∏™ÂÜôÊ≥ïÂÖºÂÆπÁÅ´Áãê
			a.dispatchEvent(new MouseEvent('click', {
				bubbles: true,
				cancelable: true,
				view: window
			}))
			window.URL.revokeObjectURL(data)
		})
	}
	//ÂÆ°Ê†∏
	import Approval from '@/components/common/approval.vue'
	const approvalRef = ref(null)
	const approvalClick = (row) => {
		nextTick(() => {
			approvalRef.value.approvalClick(row )
		})
	}
	import jingsairichengFormModel from '@/views/jingsairicheng/formModel'
	const jingsairichengFormModelRef = ref(null)
    const jingsairichengCrossAddOrUpdateHandler = (row,type,crossOptAudit,crossOptPay,statusColumnName,tips,statusColumnValue) => {
		if(crossOptAudit=='ÊòØ'&&row.sfsh!='ÊòØ') {
			context?.$toolUtil.message('ËØ∑ÂÆ°Ê†∏ÈÄöËøáÂêéÂÜçÊìç‰ΩúÔºÅ','error')
			return
		}
		if(statusColumnName!=''&&!statusColumnName.startsWith("[")) {
			var obj = row
			for (var o in obj){
				if(o==statusColumnName && obj[o]==statusColumnValue){
					context?.$toolUtil.message(tips,'error')
					return;
				}
			}
		}
		nextTick(()=>{
			jingsairichengFormModelRef.value.init(row.id,'cross','Êó•Á®ã',row,'bisaibaoming',statusColumnName,tips,statusColumnValue)
		})
    }
	import bisaijishutongjiFormModel from '@/views/bisaijishutongji/formModel'
	const bisaijishutongjiFormModelRef = ref(null)
    const bisaijishutongjiCrossAddOrUpdateHandler = (row,type,crossOptAudit,crossOptPay,statusColumnName,tips,statusColumnValue) => {
		if(crossOptAudit=='ÊòØ'&&row.sfsh!='ÊòØ') {
			context?.$toolUtil.message('ËØ∑ÂÆ°Ê†∏ÈÄöËøáÂêéÂÜçÊìç‰ΩúÔºÅ','error')
			return
		}
		if(statusColumnName!=''&&!statusColumnName.startsWith("[")) {
			var obj = row
			for (var o in obj){
				if(o==statusColumnName && obj[o]==statusColumnValue){
					context?.$toolUtil.message(tips,'error')
					return;
				}
			}
		}
		nextTick(()=>{
			bisaijishutongjiFormModelRef.value.init(row.id,'cross','ËÆ∞ÂΩï',row,'bisaibaoming',statusColumnName,tips,statusColumnValue)
		})
    }
	import bisaichengjiFormModel from '@/views/bisaichengji/formModel'
	const bisaichengjiFormModelRef = ref(null)
    const bisaichengjiCrossAddOrUpdateHandler = (row,type,crossOptAudit,crossOptPay,statusColumnName,tips,statusColumnValue) => {
		if(crossOptAudit=='ÊòØ'&&row.sfsh!='ÊòØ') {
			context?.$toolUtil.message('ËØ∑ÂÆ°Ê†∏ÈÄöËøáÂêéÂÜçÊìç‰ΩúÔºÅ','error')
			return
		}
		if(statusColumnName!=''&&!statusColumnName.startsWith("[")) {
			var obj = row
			for (var o in obj){
				if(o==statusColumnName && obj[o]==statusColumnValue){
					context?.$toolUtil.message(tips,'error')
					return;
				}
			}
		}
		nextTick(()=>{
			bisaichengjiFormModelRef.value.init(row.id,'cross','ÊàêÁª©',row,'bisaibaoming',statusColumnName,tips,statusColumnValue)
		})
    }
	//Êü•ËØ¢ÂÆ°Ê†∏Áä∂ÊÄÅÂàóË°®
	const approvalLists = ref([])
	//ÂàùÂßãÂåñ
	const init = () => {
        approvalLists.value = "ÊòØ,Âê¶,ÂæÖÂÆ°Ê†∏".split(',');
		getList()
	}
	init()
</script>
<style lang="scss" scoped>
	// Â∫èÂè∑ËÉ∂ÂõäÊ†∑Âºè
	.index_pill{
		width: 36px;
		height: 36px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
